import React from "react"
import { Link, graphql } from "gatsby"
import { Helmet } from "react-helmet"
import posed from "react-pose"
import closeSVG from "../images/close.svg"
import Layout from "../components/layout"
import styles from "../styles/summaries.module.css"
import BlogSummary from "../components/blog-summary"

const Box = posed.div({
  closed: { y: "300px" },
  open: { y: "0px", transition: { duration: 600 } },
})

const Back = posed.div({
  hoverable: true,
  pressable: true,
  init: {
    scale: 1,
  },
  hover: {
    scale: 1.2,
  },
  press: {
    scale: 1.1,
  },
})

export default ({ data }) => (
  <Layout>
    <Helmet>
      <meta charSet="utf-8" />
      <title>Gaston Hummel</title>
    </Helmet>
    <Box className="container" initialPose="closed" pose="open">
      <div className="headerOther">
        <Back>
          <Link to="/">
            <img src={closeSVG} alt="back" height="32" width="32" />
          </Link>
        </Back>
        <h2 className="center">Gaston's Blog</h2>
      </div>
      <div className={styles.summaries}>
        {data.allMarkdownRemark.edges.map(({ node }) => (
          <BlogSummary
            key={node.id}
            title={node.frontmatter.title}
            date={node.frontmatter.date}
            image={node.frontmatter.svg}
            video={node.frontmatter.video}
            excerpt={node.excerpt}
            link={node.fields.slug}
          ></BlogSummary>
        ))}
      </div>
    </Box>
  </Layout>
)

export const query = graphql`
  query {
    allMarkdownRemark(
      sort: { fields: frontmatter___date, order: DESC }
      filter: { frontmatter: { type: { eq: "blogs" } } }
    ) {
      totalCount
      edges {
        node {
          id
          frontmatter {
            title
            date(formatString: "DD MMM YYYY")
            video
            svg
          }
          fields {
            slug
          }
          excerpt(pruneLength: 200)
          html
        }
      }
    }
  }
`
